ul {
	height:9em; width:42em;
    position: relative;
}

li { margin:0;
    padding:0;
    list-style:none;
    position:relative;
    float:left;
    text-align: center; }

ul .white {
	height:8em; width:4em; z-index:1;
    border-left:1px solid black;
    border-right: 1px solid black;
	border-bottom:2px solid black;
	border-radius:0 0 5px 5px;
	-webkit-box-shadow:-1px 0 0 rgba(255,255,255,0.8) inset, 0 0 5px #ccc inset, 0 0 3px rgba(0,0,0,0.2);
	        box-shadow:-1px 0 0 rgba(255,255,255,0.8) inset, 0 0 5px #ccc inset, 0 0 3px rgba(0,0,0,0.2);
	background: -webkit-gradient(linear, left top, left bottom, from(#eee),to(#fff));
	background: -webkit-linear-gradient(top, #eee 0%,#fff 100%);
	background: -o-linear-gradient(top, #eee 0%,#fff 100%);
	background: linear-gradient(top, #eee 0%,#fff 100%);
	line-height: 200px;
}

ul .white:active {
	border-top:1px solid #777;
	border-left:1px solid #999;
	border-bottom:1px solid #999;
	-webkit-box-shadow:2px 0 3px rgba(0,0,0,0.1) inset, -5px 5px 20px rgba(0,0,0,0.2) inset,0 0 3px rgba(0,0,0,0.2);
	        box-shadow:2px 0 3px rgba(0,0,0,0.1) inset, -5px 5px 20px rgba(0,0,0,0.2) inset,0 0 3px rgba(0,0,0,0.2);
	background: -webkit-gradient(linear, left top, left bottom, from(#fff),to(#e9e9e9));
	background: -webkit-linear-gradient(top, #fff 0%,#e9e9e9 100%);
	background: -o-linear-gradient(top, #fff 0%,#e9e9e9 100%);
	background: linear-gradient(top, #fff 0%,#e9e9e9 100%);
}

.black {
    color: white;
	height:4em; width:2em; margin:0 0 0 -1em; z-index:2;
	border:1px solid #000;
	border-radius:0 0 3px 3px;
	-webkit-box-shadow:-1px -1px 2px rgba(255,255,255,0.2) inset, 0 -5px 2px 3px rgba(0,0,0,0.6) inset, 0 2px 4px rgba(0,0,0,0.5);
	        box-shadow:-1px -1px 2px rgba(255,255,255,0.2) inset, 0 -5px 2px 3px rgba(0,0,0,0.6) inset, 0 2px 4px rgba(0,0,0,0.5);
	background:-webkit-linear-gradient(45deg, #222 0%,#555 100%);
	background:-o-linear-gradient(45deg, #222 0%,#555 100%);
	background:linear-gradient(45deg, #222 0%,#555 100%);
}

.black:active {
	-webkit-box-shadow:-1px -1px 2px rgba(255,255,255,0.2) inset, 0 -2px 2px 3px rgba(0,0,0,0.6) inset, 0 1px 2px rgba(0,0,0,0.5);
	        box-shadow:-1px -1px 2px rgba(255,255,255,0.2) inset, 0 -2px 2px 3px rgba(0,0,0,0.6) inset, 0 1px 2px rgba(0,0,0,0.5);
	background:-webkit-gradient(linear, left top, right top, from(#444),to(#222));
	background:-webkit-linear-gradient(left, #444 0%,#222 100%);
	background:-o-linear-gradient(left, #444 0%,#222 100%);
	background:linear-gradient(left, #444 0%,#222 100%);
}

.a,.g,.f,.d,.c { margin:0 0 0 -1.1em;  }



